header {
  /* display: flex; */
  padding: 10px;
  position: fixed;
  top: 0;
  width: 100%;
}

header div {
  width: 33%;
  float: left;
}

header .language-selector {
  text-align: center;
  padding: 10px 0px;
}

header .theme-selector {
  text-align: right;
}

.body {
  font-size: 48px;
}

.language-selector,
.theme-selector {
  cursor: pointer;
}

.body>div {
  padding: 140px;
  text-align: center;
  height: 100 hv;
}

.theme-selector .label {
  top: -10px;
  position: relative;
  padding: 0px 10px;
}

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input {
  display: none;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {

  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked+.slider {
  background-color: #2196F3;
}

input:focus+.slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked+.slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

@media only screen and (max-width: 600px) {
  header div {
    width: 100%;
  }

  .language-selector,
  header .theme-selector,
  .logo-container {
    text-align: center;
  }
}
